import b2bFooter from "../../components/b2bfooter.vue";
import skyRaty from "../../components/skyraty.vue";
import shopItem from "../../components/shop-item.vue";
var query, sortquery, sortTop;
export default {
	components: {
		b2bFooter,
		skyRaty,
		shopItem
	},
	data() {
		return {
			localAddress:"厦门瑞景商业广场",
			pageData: {},
			keyword: "",
			pageLoad: false,
			shopList: {},
			orderLabel: "综合排序",
			sortNavBoxFix: "",
			zhpxBoxClass: "",
			orderTab: "zhpx",
			choiceBoxClass: "",
			orderby:"",
			orderList:"",
			isFirst:true,
			per_page:0,
			choiceClass:[],
			filterList:[],
			priceList:[],
			priceItem:"",
			discList:[],
			discItem:""
		}
	},
	onLoad: function(ops) {
		this.localAddress=this.app.getLocalAddress();
		query = uni.createSelectorQuery().in(this);
		this.getPage();
		this.getList();
		this.getFilter();

	},
	onPageScroll: function(e) {
		var that = this;
		if (e.scrollTop >= sortTop) {
			if (this.sortNavBoxFix == '') {
				this.sortNavBoxFix = "sortNavBoxFix";
			}
		} else {
			if (this.sortNavBoxFix == 'sortNavBoxFix') {
				this.sortNavBoxFix = "";
			}
		}
	},
	onReachBottom:function(){
		this.getList();
	},
	methods: {
		choiceFinish:function(){
			this.isFirst=true;
			this.per_page=0;
			this.choiceBoxClass="";
			this.getList();
		},
		choiceClear:function(){
			var that=this;
			var choiceClass=this.choiceClass;
			for(var i in choiceClass){
				choiceClass[i]="";
			}
			this.choiceClass={};
			setTimeout(function(){
				that.choiceClass=choiceClass;
			},30)
			that.discItem="";
			that.priceItem="";
			 
			
		},
		setDisc:function(it){
			if(this.discItem==it){
				this.discItem="";
			}else{
				this.discItem=it;
			}	
		},
		setPrice:function(it){
			if(this.priceItem==it){
				this.priceItem="";
			}else{
				this.priceItem=it;
			}			
		},
		checkChoice:function(index){
			var that=this;
			var choiceClass=this.choiceClass;
			if(choiceClass[index]==''){
				choiceClass[index]="choice-item-active"
				
			}else{
				choiceClass[index]=""
			}
			this.choiceClass={};
			setTimeout(function(){
				that.choiceClass=choiceClass;
			},30)
			
		},
		setOrder: function(v, k) {
			this.zhpxBoxClass = "";
			
			this.orderLabel = v;
			this.orderTab = 'zhpx';
			this.orderby=k;
			this.per_page=0;
			this.isFirst=true;
			this.getList();

		},
		setOrderTab: function(t) {
			this.orderTab = t;
			this.orderby=t;
			this.zhpxBoxClass = "";
		
			this.per_page=0;
			this.isFirst=true;
			if (t == 'choice') {
				if(this.choiceBoxClass==""){
					this.choiceBoxClass = "block";
					uni.pageScrollTo({
						scrollTop: sortTop
					})
				}else{
					this.choiceBoxClass="";
				}
				
				
			} else {
				this.choiceBoxClass="";
				this.getList();
			}


		},
		
		toggleOrder: function() {
			this.choiceBoxClass="";
			if (this.zhpxBoxClass == '') {
				this.zhpxBoxClass = "block";
				uni.pageScrollTo({
					scrollTop: sortTop
				})
			} else {
				this.zhpxBoxClass = "";
			}
		},
		goAddr:function(){
			uni.navigateTo({
				url:"../../pageb2b/b2b_addr/index"
			})
		},
		search: function() {
			uni.navigateTo({
				url: "../../pageb2b/b2b_search/index?keyword=" + encodeURI(this.keyword)
			})
		},
		goShop: function(shopid) {
			uni.navigateTo({
				url: "../../pageb2b/b2b_shop/index?shopid=" + shopid
			})
		},
		goProduct: function(id) {
			uni.navigateTo({
				url: "../../pageb2b/b2b_product/show?id=" + id
			})
		},
		gourl: function(url) {
			uni.navigateTo({
				url: url
			})
		},
		getPage: function() {
			var latlng=this.app.getGps();
			var that = this;
			that.app.get({
				url: this.app.apiHost + "/module.php?m=b2b&ajax=1",
				data:{
					lat:latlng.lat,
					lng:latlng.lng
				},
				success: function(res) {						
					that.pageData = res.data;
					that.pageLoad = true;
					
					
					setTimeout(function() {




						query.select('#sortNavBox').boundingClientRect(res => {
							sortTop = res.top;
							console.log(sortTop)
						}).exec();

					}, 300)

				}
			})
		},
		getFilter:function(){
			var that = this;
			that.app.get({
				url: that.app.apiHost + "/module.php?m=b2b_shoplist&a=Filter&ajax=1",
				success:function(res){
					that.orderList=res.data.orderList;
					that.discList=res.data.discList;
					that.filterList=res.data.filterList;
					that.priceList=res.data.priceList;
					that.choiceClass=Array();
					for(var i in that.filterList){
						that.choiceClass[i]="";
					}
				}
			})
		},
		getList: function() {
			var that = this;
			var latlng=this.app.getGps();
			if(that.per_page==0 && !that.isFirst){
				return false;
			}
			var choiceStr="";
			for(var k in that.choiceClass){
				if(that.choiceClass[k]!=''){
					if(choiceStr!=""){
						choiceStr+=",";
					}
					choiceStr+=k;
				}
			}
			that.app.get({
				url: that.app.apiHost + "/module.php?m=b2b_shoplist&a=list&ajax=1",
				data: {
					discount:that.discItem,
					price:that.priceItem,
					choice:choiceStr,
					orderby:that.orderby,
					lat:latlng.lat,
					lng:latlng.lng
				},
				dataType: "json",
				success: function(res) {
					 
					
					that.per_page=res.data.per_page;
					if(that.isFirst){
						that.isFirst=false;
						that.shopList = res.data.shopList;
					}else{
						var list=that.shopList;
						for(var i in res.data.shopList){
							list.push(res.data.shopList[i]);
						}
						that.shopList=list;
					}
				}
			})
		}
	}
}
